/*
--label-sale-bg-color
--label-hot-bg-color
--label-new-bg-color
--label-sale-color
--label-hot-color
--label-new-color
*/
@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin rotate($rotate) { 
  transform: rotate($rotate);
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
} 
@mixin clamp($bb) {
  display: -webkit-box;
  -webkit-line-clamp: $bb;
  -webkit-box-orient: vertical;
  overflow: hidden;
} 

.slider-list {
	display: flex;
}
.card {
	margin-bottom: 20px;
	@media (min-width: 750px) {
		margin-bottom: 50px;
	}
}
product-card {
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 100%;
	main {
		padding-top: 15px;
		position: relative;
		height: 100%;
		width: 100%;
		.card-group {
			.icon-loading {
				path {
					fill: #fff;
				}
			}
		}
	}
	.main-card {
		height: 100%;
		display: flex;
		flex-direction: column;
		.product-card-form {
			margin-top: auto;
			width: 100%;
			max-width: 140px;
		}
	}
	.card-image {
		.absolute-item {
			z-index: 1;
		}
		.btn {
			padding: 0;
			min-width: 36px;
		}
		.card-group {
			>cart-form { 
				@include margin-end(10px);
			}
			>a {
				border-color: var(--primary-color);
				margin: 0;
				svg {
					path {
						stroke: var(--primary-color);
					}
				}
			}
		}
	}
	.product-reviews-badge {
		.spr-badge {
			margin: 3px 0;
		}
		.spr-badge-caption {
			display: none;
		}
		.spr-icon {
			top: 0;
		}
	}
	.card-title {
		font-size: 13px;
		line-height: 1.5em;
		font-weight: 400;
		margin-bottom: 5px;
		a {
			color: var(--text-color);
			display: block;
		}
	}
	.limit-1-line {
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.limit-2-line {
		white-space: normal;
		text-overflow: unset;
	}
	.limit-3-line {
		white-space: normal;
		text-overflow: unset;
	}
	.product-weight {
		font-size: 13px;
		font-weight: 400;
		margin-bottom: 5px;
	}
	.product-price {
		margin-top: 10px;
		margin-bottom: 10px;
		font-size: 14px;
		price-item {
			line-height: 1.2em;
		}
	}
	.label {
		padding: 2px 10px;
		font-size: 11px;
		line-height: 1.47em;
		border-radius: var(--border-radius);
		display: block;
		font-weight: 700;
		min-width: 46px;
		text-align: center;
		margin-bottom: 8px;
	}
	.card-group {
		display: flex;
		align-items: center;
		margin-top: auto;
		>form {
			width: 100%;
			@media (min-width: 750px) {
				width: auto;
			}
		}
		>a {
			flex: 0 0 36px;
			height: 36px;
			border: 1px solid #E6E6E6;
			border-radius: 5px;
			display: flex;
			align-items: center;
			justify-content: center;
			@include margin-start(10px);
			z-index: unset;
			>div {
				display: flex;
			}
			svg {
				width: 15px;
				height: 15px;
			}
			&.arn_added {
				border-color: var(--primary-color);
				svg {
					path {
						stroke: var(--primary-color);
					}
				}
			}
		}
	}
	cart-form {
		.btn {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			height: 36px;
			width: 100%;
			max-width: 140px;
			position: relative;
			.text {
				@include padding-start(10px);
				line-height: 1.4em;
				@include clamp(1); 
			}
			.icon {
				display: flex;
				width: 15px;
				height: 15px;
			}
			&[disabled] {
				pointer-events: none;
				opacity: 0.7;
			}
			.icon-loading {
				display: none;
				path {
					stroke: none;
				}
			}
		}
		.loading {
			.icon-cart {
				display: none;
			}
			.icon-loading {
				display: block;
			}
		}
	}
	.quantity-select {
		display: flex;
		align-items: center;
		background: var(--miscellaneous-color);
		border-radius: var(--border-radius);
		max-width: 140px;
		position: relative;
		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			appearance: none;
			margin: 0;
		}
		input {
			border: 0;
			padding: 0;
			width: calc(100% - 80px);
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			height: 36px;
		}
		button {
			padding: 5px;
			width: 40px;
			height: 36px;
			display: flex;
			align-items: center;
			justify-content: center;
			transition: 0.2s all ease-in-out;
			&:hover {
				opacity: 0.8;
			}
		}
		.icon-minus {
			width: 7px;
			height: 7px;
		}
		>.icon-loading {
			display: none;
			width: 15px;
			height: 15px;
			@include ab(auto,50%,n,n);
			@include margin-start(-7.5px);
		}
		&.loading {
			.icon-loading {
				display: block;
			}
			input {
				opacity: 0;
				visibility: hidden;
			}
		}
	}
	&.hover-back-fadeIn.showing {
		.card-image {
			.card-group {
				bottom: 0;
				opacity: 1;
				visibility: visible;
			}
		}
	}
	@media (min-width: 750px) {
		.card-title {
			font-size: 15px;
		}
		.product-price {
			font-size: 16px;
		}
	}
	@media (min-width: 1000px) {
		.card-image {
			overflow: hidden;
		}
	}
}
.hover-zoomIn {
	@media (min-width: 1000px) {
		.card-image {
			img {
				transition: all 1s cubic-bezier(0,.51,.25,1);
			}
			&:hover {
				img {
					transform: scale(1.05);
				}
			}
		}
	}
}
.hover-back-fadeIn {
	.card-image { 
		position: relative;
		a {
			>figure:nth-child(2) {
				display: none;
			}
		}
	}
	@media (min-width: 1000px) {
		.card-image {
			a {
				>figure {
					transition: 0.25s all ease-in-out;
				}
				>figure:nth-child(2) {
					display: block;
					@include ab(0,0,0,0);
					z-index: -1;
					opacity: 0;
					visibility: hidden;
				}
			}
			&:hover {
				a {
					>figure:nth-child(2) {
						z-index: 1;
						opacity: 1;
						visibility: visible;
					}
					>figure:not(:last-child) {
						opacity: 0;
						visibility: hidden;
					}
				}
			}
		}
	}
}